.container{
    position: fixed;
    top: 0vh;
    left:0vw;
    height: 100vh;
    width: 100vw;
    background-color: red;
    z-index: 2500;
    background-color: #fff;
    display: flex;
    flex-direction: row;justify-content: space-between;
    align-items: center;
    box-shadow: 2px 2px 1px #ddd;
}
.displayCenter{
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: row;
}

.leftImg{
    position: absolute;
    top: 0;
    left: 0;
    width:30rem;
}
.leftCorner{
    top: 2rem;
    left: 2rem;
    width: 12rem;
    position: absolute;
}
.captcha{
    width: 100%;
    margin: 1rem 0;
    display: flex;
    flex-direction: row;
    justify-content:flex-start;
    margin-left: 5rem;
    align-items: center;
}
.hr{
    width: 100%;
    height: 1px;
    background-color: #eee;
    margin: 2rem;
    align-self: flex-start;
}
h2{
    width: 100%;
    font-weight: 500;
    color: #111;
    font-size: 2.2rem;
    text-align: left;
    margin: 3rem 0;
}
h3{
    width: 100%;
    font-weight: 500;
    color: #111;
    font-size: 2.0rem;
    text-align: left;
}
h5{
    width: 100%;
    font-weight: 300;
    color: #545455;
    font-size: 1.6rem;
    text-align: left;
    margin: 1rem 0;
}
.left{
    flex : 1;
    min-width: 50%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}
.left img{
    width: 70%;
}
.phoneInput{
    width: 100%;
    margin: 5% 0;
    /* background-color: green; */
    display: flex;
    flex-direction: row;
    justify-content: flex-start;
    align-items: center;
}
.phoneInput input{
    width: auto;
    height: 4.2rem;
    border: 1px solid #ddd;
    box-shadow: 2px 2px 2px #eee;
    border-radius: 4px;
    font-size: 1.6rem;
    padding: 0 2rem;
    margin: 0;
    
}
.phoneInput span{
    width: 4.5rem;
    height: 4rem;
    padding: 1rem;
    border: 1px solid #ddd;
    box-shadow: 2px 2px 2px #eee;
    border-radius: 4px;
    font-size: 1.6rem;
    background-color: #fff;
}
.button{
    border: 0;
    background-color: #00b5bf;
    color: white;
    border-radius: 50rem;
    padding: 1rem 5rem;
    font-size: 1.8rem;
    margin: auto 0px;
}
.button:hover{
    transform: scale(1.01);
}
.right {
    width: 45rem;
    height: 100%;
    padding: 1rem 10%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    /* background-color: red; */
}
.box{
     width: 40rem;
    /* height: 90vh; */
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.rightBox{
    width: 40rem;
    /* height: 90vh; */
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    /* border: 1px solid #eee; */
   
    /* background-color: orange; */
}
.otpInput{
    margin: 0 1rem;
    width: 3.6rem !important;
    height: 3.6rem !important;
    color: red;
    
}
.inputClass{
    width: 35rem;
    /* color:red !important; */
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
    /* background-color: red; */
    margin: 3rem 5rem;
}
.footer{
    font-size: 1.6rem;
    position: fixed;
    bottom: 1rem;
    left: 50%;
    transform: translateY(-50%);
}

.buttonBox{
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    width: auto;
    position: fixed;
    top: 2rem;
    right: 5rem;
}

.roundButton{
    
    border-radius: 10rem;
    padding: 0.6rem 3rem;
    font-size: 1.6rem;
    color: #00b5bf;
    border: 1px solid #00b5bf;
    background: #fff;
    margin: 0 1rem;
    cursor: pointer;
}

.roundButton:hover {
    transform: scale(1.05);
}