.container{
    width: 100%;
    height: 100%;
    background : url('../../../assets/images/success.png') no-repeat;
    background-size: auto  80%;
    background-position: top;
}

.box{
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    width: 100%;
    height: 100%;
}

.leftBox{
    width: 40rem;
}
.leftBox img{
    height: 50vh;
    width: auto;
}
.rightBox{
    position: absolute;
    bottom: 5vh;
    left: 0;
    width: 100%;
    height: auto;
    padding: 1rem;
    border-radius: 1rem;
    background-color: rgba(255,255,255,0.5);
    box-shadow: 2px 2px 2px #eee;
    
}
.center{
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    width: 100%;
    /* background-color: green; */
}
.rightBox a{
    text-decoration: none;
    font-size: 1.8rem;
    color: red;
    text-decoration: underline;
    /* transform: translateY(-50%); */
}
.successIcon{
    width: 100%;
    margin-left: 40%;
    margin-top: 2rem;
}

.rightBox h2{
    width: 100%;
    text-align: center;
    font-size: 1.7rem;
    font-size: 500;
}
.rightBox h3{
    width: 100%;
    text-align: center;
    font-size: 1.5rem;
    font-size: 300;
}
.detailsBox{
    padding: 1rem;
    box-shadow: 2px 2px 2px #eee;
    border: 1px solid #eee;
    margin: 1rem 0;
}
.displayFlexRow{
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    padding: 0.2rem 0;
    width: 100%;
}
.rightBox p{
    font-size: 1.4rem;
    font-weight: 300;
}