.container{
    /* box-shadow: 0px 5px 5px #eee, 0px -2px 2px #ddd; */
    height: 100% !important;
    width:  100%  !important;
    /* position: fixed;
    left: 7rem;
    top: 0;
    z-index: 1500; */
    /* background-color: red; */
}
.container img{
    width: 18rem;
    height: auto;
}
.input{
    width: 55rem !important;
    height: auto;
    position: relative;
    /* background-color: orange; */
}
.input input{
    width: 100%;
    border-radius: 5px;
    border: 1px solid #ddd;
    height: 4rem;
    font-size: 1.6rem;
    color: #545454;
    padding: 1rem 2rem;
    box-shadow: 1px 1px 1px #ddd;
}
.input span{
    /* background: red; */
    position: absolute;
    top: 5px;
    right: 0;
}

.button{
    font-size: 1.6rem;
    height: 4rem;
    border: 0px;
    margin: 0 1rem;
    background-color: #00c2cb;
    color: #fff;
    padding: 0 3rem;
    border-radius: 5px;

}
.button:hover{
    background-color: #fff;
    border: 1px solid #00c2cb;
    color: #00c2cb;
}
.rightBox{
    width: 30rem !important;
    margin: 0 2rem;
   display: flex;
   flex-direction: row;
   justify-content: space-evenly;
   align-items: center;
   /* background-color: red; */
}
.displayFlexCol{
    display: flex;
    flex-direction:  column;
    justify-content: space-between;
    align-items: center;
}
.categoriesBox{
    padding-top: 0.5rem;
}
.categoriesList{
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    width: 100%;
    overflow-x: scroll;
    overflow-y: hidden;
    margin: 2rem 0;
    box-shadow: 0px 2px 1px #ddd;
    
}
.categoriesList img{
    width: 4rem;
    height: 4rem;
}
.categoriesItem{
    width: auto;
    flex:  1;
    height: 8rem;
    cursor: pointer;
    margin: 0 0.5rem;
}
.iconBox{
    position: relative;
    padding: 1rem 0.5rem;
    cursor: pointer;
}
.iconBox > span{
    position: absolute;
    top: 1rem;
    right: 1rem;
    font-size: 1.3rem;
    color: #fff;
    background-color: red;
    width: 1.8rem;
    height: 1.8rem;
    text-align: center;
    border-radius: 1rem;

}
.categoriesItem:hover{
    transform: scale(1.01);
}
.categoriesItem li{
    list-style-type: none;
    font-size: 1.2rem;
    text-align: center;
}

.roundButton{
    
    border-radius: 10rem;
    padding: 0.6rem 3rem;
    font-size: 1.6rem;
    color: #00b5bf;
    border: 1px solid #00b5bf;
    background: #fff;
    margin: 0 1rem;
    cursor: pointer;
}

.roundButton:hover {
    transform: scale(1.05);
}

@media screen and (max-width : 480px){
    .container{
        width: 100% !important;
        height: auto !important;
        padding: 1rem 0.5rem;
    }
    .container img{
        width: 15rem;
    }
    .input{
        order:  3 !important;
        padding: 1rem;
        width: 99% !important;
    }
    .input span{
        top: 1.5rem;
        right: 2rem;
        z-index: 1500;
    }
    .rightBox{
        width: 150px !important;
        /* background-color: orange; */
    }
    .categoriesBox{
        padding: 0;
        margin: 0;
    }
   .categoriesList img{
        width: 5rem;
        height: 5rem;
    }
    .categoriesItem{
        width: auto;
        flex:  1;
        height: 10rem;
        cursor: pointer;
        margin: 0 1.5rem;
    }
}