.container{
    width: 100%;
    height: auto;
    /* overflow-y: hidden; */
}

.displayFlexRow{
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
}
.displayFlexCol{
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: center;
}
.filterBlock{
    width: 30rem;
    min-height: 80vh;
    height: 100%;
    border : 1px solid #eee;
    box-shadow: 1px 1px 1px #eee;
    margin: 0 0.5rem;
    padding: 2rem;
}
.productBlock{
    width: 100%;
    display: flex;
    flex-direction: row;
    justify-content: flex-start;
    align-items: flex-start;
    flex-wrap: wrap;
    padding: 1rem;
    /* background-color: red; */
}
.itemBlock{
    flex : 5;
    min-width: 20rem;
    max-width: 22rem;
    min-height: 28rem;
    max-height: 30rem;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    flex-wrap: wrap;
    border: 1px solid #ddd;
    box-shadow: 1px 1px 5px 1px #ddd;
    border-radius: 1rem;
    margin: 0.5rem 0.5rem;
    padding: 1rem;display: inline-block;
}
.itemBlock:hover{
    transform: scale(1.01);
    cursor: pointer;
    transition: all 500ms ease-in-out;
}
.itemBlock img{
    width: 15rem;
    height: 15rem;
    margin: 0 auto;
}
.itemBlock h3{
    width: 100%;
    font-size: 1.4rem;
    font-weight: 400;
    padding-bottom: 0.4rem;
    border-bottom: 1px solid #aaa;
}
.itemBlock h4{
    font-size: 1.3rem;
    font-weight: 300;
    margin-right: 0.5rem;
}
.itemBlock p{
     width: 100%;
    font-size: 1.2rem;
    font-weight: 300;
}

.filterBox{
    padding: 1rem;
    width: 100%;
    height: 100%;
}
.borderBox{
    border: 1px solid #ddd;
    padding: 0.5rem 1rem;
    margin: 5px 0;
    border-radius: 5px;
}
.checkBox{
    width: 2rem;
    height: 2rem;
}


@media screen and (max-width : 480px){
    .container{
        max-width: 100vw;
        max-height: 100vh;
        overflow-y: scroll;
    }
    .filterBlock{
        display: none;
    }
    .productBlock{
        flex-wrap: wrap;
        margin: 0;
        padding: 0;
    }
    .itemBlock{
        flex : 2;
        min-width: 18rem;
        max-width: 19rem;
        min-height: 28rem;
        max-height: 30rem;
    }
    
}