.container{
    width: 2rem;
    height: 2rem;
    margin : 0rem 5px;
}
.container  > use{
    fill: #00c2cb;
}
.container:hover{
    transform: scale(1.1);
    cursor: pointer;
}
@media screen and (max-width : 480px){
    .container{
         width: 2.8rem !important;
        height: 2.8rem !important;
    }
}