.container{
    padding:  1rem;
}
.displayFlexRow{
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    width: 100%;
}
.displayFlexRowEqual{
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    width: 100%;
}
.displayFlexRowEqual:nth-child(n){
    width: 50%;
    margin: 0.5rem 0;
}
.leftBox{
    flex: 1;
    height: 100%;
    padding: 1rem;
}
.gap{
    display: block;
    width: 100%;
    height: 2rem;
}
.rightBox{
    width: 80rem;
    height: 60vh;
    padding: 1rem;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: flex-start;
    flex-wrap: wrap;
}
.listItems{
    width: 50%;
    height: 60vh;
    overflow-y: scroll;
}
.orderDetails{
    width: 50%;
    height: 70vh;
    padding: 1rem;
    overflow-y: scroll;

}
.userDetails{
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    flex-wrap: wrap;
    background-color: rgba(10, 168, 186,0.3);
    width: 100%;
    height: auto;
    padding: 1rem;
}
.bgImg{
    width: auto;
    height: 60vh;
}
.borderBox{
    border: 1px solid rgba(10, 168, 186,0.3);
    padding: 0.8rem 1rem;
    margin: 5px 0;
    border-radius: 5px;
    background-color: rgba(10, 168, 186,0.3);
}
h6{
    display: inline-block;
    color: #554455;
    font-weight: 400;
}
.addressBox{
    background-color: #fff;
    width: 100%;
    padding: 1rem;
    margin: 0.5rem 0;
}
.bottomBox{
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    width: 100%;
    background-color: #eee;
    padding: 1rem 0.5rem;
    /* margin-top: 15vh; */
}
p{
    font-size: 1.4rem;
    margin: 2px 0;
    padding: 0;
    color: #111;
}
.hr{
    width: 100%;
    height: 2px;
    background-color: #ddd;
}
.listBlock{
    width: 99%;
    padding: 1rem;
    box-shadow: 2px 2px 2px #ddd ;
    border-radius: 5px;
    margin: 4px 1px;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    cursor: pointer;
}
.imageContainer{
    width:  25%;
    display: block;
}
.itemBox{
    width: 60%;
    display: block;
}
.listImg{
    width: 8rem;
    height: auto;
}