.buttonOne{
    width: auto ;
    min-width: 15rem;
    min-height: 4rem;
    padding: 1rem 3rem;
    background: #00c2cb;
    border-radius: 0.5rem;
    color: #fff;
    border: 0px;
    font-size: 1.6rem;
    cursor: pointer;
}

.roundButton{
    border-radius: 5rem;
    border: 1px solid #aaa;
    background-color: #fff;
    padding: 0.8rem 3rem;
    color: #545454;
    font-size: 1.5rem;
    cursor: pointer;
}
.roundButton:hover{
    transform: scale(1.05);
}