.container{
    width: 100%;
    height: 100%;
    position: relative;
    overflow-x: scroll;
}
.itemBlock{
    flex : 5;
    width: 100%;
    max-height: 3rem;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    border-bottom: 1px solid #ddd;
    margin: 1rem 1rem;
    padding: 1rem 1rem 1rem 0;
}
.displayFlexRow{
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
}
.itemBlock img{
    width: 15rem;
    height: 15rem;
    margin: 0 auto;
}
.itemBlock h3{
    font-size: 1.4rem;
    font-weight: 400;
}
.itemBlock h4{
    font-size: 1.3rem;
    font-weight: 300;
    margin-right: 0.5rem;
}
.itemBlock p{
    font-size: 1.2rem;
    font-weight: 300;
}
.orderBlock{
    width: 100%;
    height: 100%;
     overflow-y: scroll;
}
.borderBox{
    border: 1px solid #ddd;
    border-radius: 1rem;
    padding: 1rem;
    width: 100%;
    height: auto;
    margin-bottom: 4px;
}
