.container{
    width: 100%;
    height: 100%;
    z-index: 2500;
}

.displayFlexRow{
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: flex-start;
}
.displayFlexRowC{
      display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
}
.displayFlexCol{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    height: 100%;
}
.box{
    width: 100%;
    padding: 1rem;
    height: 100%;
}
.accountContainer{
    flex: 1;
    height: 100%;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: flex-start;
    flex-wrap: wrap;
}

.card{
    width: calc(33% - 0.5rem);
    /* flex: 2; */
    min-height: 30rem;
    display: flex;
    flex-direction: row !important;
    justify-content: space-between;
    align-items: flex-start;
    flex-wrap: wrap;
    border: 1px solid #ddd;
    box-shadow: 1px 1px 0.5px 1px #ddd;
    border-radius: 1rem;
    margin: 0.4rem 0.4rem;
    padding: 1rem;
}

p{
    font-size: 1.2rem;
    font-weight: 300;
    color: #545454;
}
.cardImg{
    width: 25rem;
}

.borderText{
    font-size: 1.3rem;
    color: #545454;
    border-bottom: 1px solid #aaa;
    padding: 0.2rem 0;
    cursor: pointer;
}
.text{
     font-size: 1.3rem;
    color: #545454;
    padding: 0.2rem 0;
    cursor: pointer;
}
.userImg{
    width: 6rem;
    height: 6rem;
    border-radius: 6rem;
    border: 1px solid #eee;
}
h6{
    font-size: 1.4rem;
    font-weight: 500;
    color: #111;
    width: 100% !important;
}

@media screen and (max-width : 480px){
    .rightBox{
        display: none !important;
    }
    .accountContainer{
        min-width: 100% !important;
        min-height: 100vh !important;
        padding-right:1rem;
    }
    .card{
        max-width: 100%;
        min-width: 100%;
    }
}