.buttonOne{
    width: auto ;
    min-width: 15rem;
    min-height: 4rem;
    padding: 1rem 3rem;
    background: #00c2cb;
    color: #fff;
    border: 0px;
    font-size: 1.6rem;
    cursor: pointer;
    border-radius: 12rem !important;
}

.roundButton{
    border-radius: 0.5rem;
    background: #00c2cb;
    border: 0;
    color: #fff;
    padding: 0.8rem 3rem;
    font-size: 1.5rem;
    cursor: pointer;
}
.roundButton:hover{
    transform: scale(1.05);
}