.header{
    width: '100%';
    font-family: 'Poppins', sans-serif;
    height: auto;
    padding: 1rem;
    /* background-color: red; */
}
.displayFlexRow{
    display: flex;
    flex-direction:  row;
    justify-content: space-between;
    align-items: center;
}
.displayFlexCol{
    display: flex;
    flex-direction:  column;
    justify-content: space-between;
    align-items: center;
}
.logo{
    width: 30rem;
    height:  auto;
}
.logo img{
    width: 15rem;
    height:  auto;
    margin: 0 7.5rem;
}
.searchBox {
    width: 50rem;
    flex: 1;
    height: 4.5rem;
    position: relative;
}
.searchBox input{
    border: 1px solid #ddd;
    border-radius: 0.4rem;
    width: 100%;
    height: 100%;
    padding: 5px 1rem;
    font-size: 1.6rem;
    box-shadow:  1px 0.5px 1px  #ddd;
    color: #545454;
}
.rightIcon{
    position: absolute;
    top: 1rem;
    right: 1rem;
}
.rightTabContainer{

}
.rightTabContainer img{
    height: 4rem;
    width: auto;
    margin: 0 1rem;
}
.loginButton{
    border: 1px solid #ddd;
    border-radius: 5px;
    background-color: #fff;
    margin: 0 1rem;
}
.loginButton h4{
    font-weight: 400;
    height: 1.6rem;
    padding: 0;
    margin: 0;
}
.userBlock{
    padding: 2px;
}
.userBlock p{
    font-size: 1.4rem;
    font-weight: 400;
    
}
.cart{
    position: relative;
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    margin: 0 0rem;
}
.cartValueBox{
    position: absolute;
    top: -1rem;
    right: 0rem;
    border-radius: 10rem;
    width: 2rem;
    height: 2rem;
    margin: 0 auto;
    text-align: center;
    padding-top: 2px;
    background-color: #ff5757;
    color: #fff;
    border: 0px;
}
.categoriesBox{
    padding-top: 0.5rem;
}
.categoriesList{
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    width: 100%;
    overflow-x: scroll;
    overflow-y: hidden;
    margin: 2rem 0;
    box-shadow: 0px 2px 1px #ddd;
    
}
.categoriesList img{
    width: 4rem;
    height: 4rem;
}
.categoriesItem{
    width: auto;
    height: 6.5rem;
    cursor: pointer;
}
.categoriesItem:hover{
    transform: scale(1.01);
}
.categoriesItem li{
    list-style-type: none;
    font-size: 1.4rem;
    text-align: center;
}


@media screen and (max-width : 480px){
    .header{
        width: 100%;
    }
    .userIcon{
        display: none;
    }
    .logoutButton{
        display: none;
    }

    .displayFlexRow{
        display: flex;
        flex-direction:  row;
        justify-content: space-between;
        align-items: center;
        flex-wrap: wrap;
    }
    .logo{
        width: 15rem;
        height:  auto;
        order: 1;
    }
    .logo img{
        width: 12rem;
        height:  auto;
        margin: 0 1rem;
    }
    .searchBox{
        order: 3;
        min-width: 100%;
        margin-top: 1rem;
        border-radius: 1rem;
    }
    .rightTabContainer img{
        display: none;
    }
    .rightTabContainer{
        order: 2;
        display: flex;
        flex-direction: row;
        justify-content: flex-end;
        width: 22rem;
    }

    .userBlock{
        display: flex;
        flex-direction: row;
        justify-content: flex-start;
    }
    .categoriesBox{
        margin: 0;
    }
    .categoriesItem{
        min-width: 9rem;
        height: 8rem;
        cursor: pointer;
        margin: 0 0.2rem;
    }
    .categoriesItem li{
        font-weight: 300;
        font-size: 1.2rem;
    }
 
}