.container{
    z-index: 2222;
    /* background-color: red; */
    width: 100%;
    height: 100vh;
    display: grid;
    grid-template-rows: repeat(24, 1fr);
    grid-template-columns: repeat(36,1fr);
    /* position: relative; */
}
.navigator{
    grid-row: 1/25;
    grid-column: 1/2;
    /* background-color: orange; */
}
.header{
    grid-row: 1/7;
    grid-column: 2/37;
    /* background-color: green; */
}
.body{
    grid-row: 7/25;
    grid-column: 2/37;
    overflow-y: scroll;
    overflow-x: scroll;
  
    /* background-color: blue; */
}


@media screen and (max-width : 480px){
    .container{
        display: flex;
        flex-direction: column;
        justify-content: flex-start;
        align-items: center;
        width: 100%;
        height: 100vh;
        overflow-y: scroll;
    }
   .navigator{
        position: fixed;
        bottom: 0;
        left: 0;
        width: 100%;
        z-index: 2500;
        background-color: #fff;
    }
    .header{
       width: 100%;
       height:auto;
       /* background-color: red; */
    }
    .body{
        width: 100%;
    }
}