.container{
    width: 100%;
    height: 100vh;
    background-color: rgba(1,1,1,0.2);
    z-index: 500;
    position: fixed;
    top: 0;
    left: 0;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}
.displayFlexRow{
    display: flex;
    flex-direction:  row;
    justify-content: space-between;
    align-items: center;
}
.displayFlexCol{
    display: flex;
    flex-direction:  column;
    justify-content: space-between;
    align-items: center;
}
.closeBox{
    position: absolute;
    top: 8vh;
    right: 19%;
    padding: 0.4rem;
    background: #fff;
    border-radius: 10rem;
}
.authBox{
    width: 60%;
    height: 80vh;
    box-shadow: 1px 1px 1px #ddd;
}

.authBlock{
    width: 100%;
    height: 100%;
    background-color: #fff;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    padding: 1rem;
}
.leftBox {
    width: 45%;
    height: 100%;
     display: flex;
    flex-direction: col;
    justify-content: center;
    align-items: center;
    position: relative;
}
.leftBoxImage img{
    width: 100%;
    height: auto;
}
.rightBox {
    flex: 1;
    height: 100%;
    padding: 3rem 5%;
    border: 1px solid #ddd;
    align-items: "center";
    background-size: cover;
    position: relative;
}
.rightBoxHeading{
    padding: 1rem;
    width: 90%;
}
.rightBoxHeading h2{
    font-size: 2.2rem;
    font-weight: 500;
}
.rightBoxHeading p{
    font-size: 1.5rem;
    font-weight: 400;
    margin : 5px 0;
    color: #545454;
}
.phoneCodeBlock{
    padding: 0.7rem;
    background-color: #fff;
    font-size: 1.5rem;
    box-shadow: 2px 2px 2px #ddd;
    border: 1px solid #ddd;
    border-radius: 5px;
    margin-right: 1rem;
}
.inputFieldBlock{
    margin: 5px 0;
}
.inputFieldBlock input{
    outline: none;
    border: 1px solid #ddd;
    width: 25rem;
    height: 4rem;
    border-radius: 5px;
    box-shadow: 2px 2px 2px #ddd;
    padding: 0 1rem;
    font-size: 1.5rem;
}
.otpInput{
    margin: 0 1rem;
    width: 3rem !important;
    height: 3.5rem !important;
    color: red;
}
.inputClass{
    color:red !important;
}
.button{
    cursor: pointer;
    font-size: 1.2rem;
    margin: 0.5rem;
    text-align: center;
}
.cover{
    position: absolute;
    top: 0;
    left: 0;
    width: "50%";
    height: auto;
}
.coverRight{
    position: absolute;
    top: 0;
    right: 0;
    width: "50%";
    height: auto;
    transform: rotate("45deg");
}