.container{
    width: 100%;
    height: 100%;
    padding: 1rem;
}
.box{
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: flex-start;
    width: 100%;
    height: auto;
}

.leftBox{
    flex : 1;
    height : 100%;
    padding: 0 1rem;
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: center;
}

.rightBox{
    padding: 1rem;
    width: 30rem;
    height: 60vh;
    box-shadow: 2px 2px 2px  #eee;
    border: 1px solid #eee;
    position: relative;
}
.rightBox p{
    font-size: 1.4rem;
   

}
.hr{
    width: 100% !important;
    height: 1px !important;
    background-color: red;
    display: block;
    box-shadow: 2px 2px 2px  #eee;
    margin: 0.5rem 0;
    padding: 1rem;
}
.rightBox > div{
    padding: 0.5rem 0.5rem;
    background-color: #fff;
    border-radius: 5px;
    margin: 0.2rem 0;
}
.displayFlexRow{
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    width: 100%;
}
.cartBox{
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    width: 95%;
    box-shadow: 2px 2px 2px  #eee;
    margin: 0.5rem 0;
}
.img img{
    width: 10rem;
}
.detailsBox{
    flex : 5;
    padding: 0 2rem;
}
.amount{
    color: green;
    font-weight: 500;
    font-size: 1.6rem;
}
.buttonBox{
    flex : 1;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: center;
}

.button{
    position: absolute;
    bottom: 1rem;
    left: 1rem;
    border: 0;
    background-color: #00c2cb;
    color: #fff;
    width: 28rem;
    display: block;
    padding: 1rem 0;
    font-size: 1.6rem;
    border-radius: 5px;
    cursor: pointer;
}

