.container{
    width: 100%;
    height: 100%;
    position: relative;
    padding: 1rem;
}
.itemBlock{
    width: 98%;
    height: 4rem;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    border: 1px solid #eee;
    margin: 0.5rem 1rem;
    padding: 1rem;
    cursor: pointer;
    border-radius: 5px;
    box-shadow: 1px 1px 1px #eee;
}
.displayFlexRow{
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
}
.itemBlock img{
    width: 15rem;
    height: 15rem;
    margin: 0 auto;
}
.itemBlock h3{
    font-size: 1.4rem;
    font-weight: 400;
}
.itemBlock h4{
    font-size: 1.3rem;
    font-weight: 300;
    margin-right: 0.5rem;
}
.itemBlock > p{
    font-size: 1.2rem;
    font-weight: 300;
    flex: 5;
}
.orderBlock{
    width: 100%;
    height: auto;
     /* overflow-y: scroll; */
}
.borderBox{
    border: 1px solid #ddd;
    border-radius: 1rem;
    padding: 1rem;
    width: 100%;
    height: auto;
    margin-bottom: 4px;
}
.trackBox{
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: flex-start;
    width: 100%;
    height: 8rem;
    position: relative;
}
.trackTextBox{
    width: 90%;
    /* background-color: red; */
}
.trackBarBox{
    display: flex;
    flex-direction: row;
    justify-content: flex-start;
    align-items: center;
    /* background-color: blue; */
    width: 100%;

}
.gap{
    display: block;
    width: 2rem;
    height: 100%;
}
.trackBar{
    position: absolute;
    top: -7rem;
    left: 0.2rem;
    width: 5px;
    height: 7rem;
    background-color: green;
    display: block;
    border-radius: 1rem;
}
.trackCircle{
    width: 1rem;
    display: block;
    height: 1rem;
    border-radius: 1rem;
    background-color: green;
}

@media screen and (max-width : 480px){
    .itemBlock{
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        min-height: 10rem;
        align-items: flex-start;
        height: auto;
    }
}