.container{
    width: 100%;
    height: 100%;
}

.displayFlexRow{
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
}
.displayFlexCol{
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: center;
}

.block{
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: flex-start;
    width: 100%;
    padding: 1rem;
    overflow-y: scroll;
}

.leftBlock{
    flex: 2;
    /* height: 86vh; */
    border-right: 1px solid #eee;
    /* background-color: red; */
}
.rightBlock{
    flex : 3;
    /* background-color: green; */
    /* height: 85vh; */
    /* overflow-y: scroll; */
    border-left: 1px solid #eee;
    padding: 1rem 2rem;

}
.sideImage{
    width: 15vh;
    padding: 1rem;
    height: 15vh;
    box-shadow: 1px 1px 1px 1px #eee;
    border: 1px solid #eee;
    border-radius: 5px;
    margin: 1rem 0;
}
.sideImage img{
    width: 100%;
    height: 100%;
}
.sideImage:hover{
    border: 1px solid #aaa;
}
.fullImage{
    width: 100%;
    height: 100%;
}
.fullImage img{
    width: 100%;
    height: 100%;
}
h6{
    font-size: 1.4rem;
    color: #545454;

}
.amount{
    color: green;
    font-size: 2.2rem;
    font-weight: 500;
    padding: 2rem 0.5rem;
}
.mrp{
    font-size: 1.6rem;
    padding: 2rem 0.5rem;
    text-decoration: line-through;
}
.discount{
    font-size: 1.6rem;
    padding: 2rem 0.5rem;
}
p{
    font-size: 1.3rem;
    color: #545454;
    line-height: 2rem;
    margin: 1rem 0;
    text-align: justify;
}
.button{
    border : 1px solid #ddd;
    padding: 0.5rem 1.2rem;
    font-size: 1.8rem;
    cursor: pointer;
}
.button:hover{
    transform: scale(1.01);
}
.greenText{
    text-align: center;
    font-size: 1.8rem;
    padding: 1rem 0;
    color: green;
}
.redText{
    text-align: center;
    font-size: 1.8rem;
    padding: 1rem 0;
    color: red;
}

.addToCart{
    flex : 1;
    padding:1rem 0.5rem;
    text-align: center;
    background-color: #eee;
    color: #111;
    margin: 1rem 0;
    font-size: 1.8rem;
    border: 0;
    cursor: pointer;

}

.buyNow{
    flex: 1;
    border: 0;
    padding:1rem 0.5rem;
    text-align: center;
    background-color: #00c2cb;
    color: #fff;
    margin: 1rem 0;
    font-size: 1.8rem;
    box-shadow: 2px 2px 1px 1px #00c2cb;
    border-radius: 3px;
    cursor: pointer;
}
table{
    width: 100%;
    height: auto;
    padding: 1rem;
    font-size: 1.4rem;
    font-weight: 400;
    text-align: left;
}
tr{

}
td, th{
    padding: 0.5rem;
    background-color: #fafafa;
}