html{
  font-size: 62.5%;
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

*,
*::after,
*::before{
  box-sizing: inherit;
  margin: 0;
  padding: 0;
}

body {
  margin: 0;
  font-family: 'Poppins', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  width: 100%;
}

code {
  font-family: 'Poppins', sans-serif;
}

h1.stylehOne{
    background: linear-gradient(to right , rgb(255, 1, 1) , rgb(255, 238, 0));
    font-size: 3rem;
    font-weight: 700;
    background-clip: text;
    color: transparent;
    text-transform: capitalize;
}
.displayFlexRow{
    display: flex;
    flex-direction:  row;
    justify-content: space-between;
    align-items: center;
}
.displayFlexCol{
    display: flex;
    flex-direction:  column;
    justify-content: space-between;
    align-items: center;
}